.cropWrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1000;
    background-color: #0D1B2A;
    display: flex;
    justify-content: center;
    align-items: center;
}

.title {
    position: absolute;
    top: 1rem;
    left: 1rem;
    color: white;
    z-index: 1;
}

.acceptCropBtn {
    position: absolute !important;
    bottom: 1rem;
    // width: 6rem;
    right: 1rem;
}